import React,{Component} from 'react'
import ReactECharts from 'echarts-for-react'; 

export default class Pie extends Component{

  getOption = () => {
    return {title: {
        text: '用戶訪問來源(折線圖)',
    },
    tooltip: {
        trigger: 'axis'
    },
    legend: {
        data: ['郵件營銷', '聯盟廣告', '視頻廣告', '直接訪問', '搜索引擎']
    },
    grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
    },
    toolbox: {
        feature: {
            saveAsImage: {}
        }
    },
    xAxis: {
        type: 'category',
        boundaryGap: false,
        data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
    },
    yAxis: {
        type: 'value'
    },
    series: [
        {
            name: '郵件營銷',
            type: 'line',
            stack: '總量',
            data: [120, 132, 101, 134, 90, 230, 210]
        },
        {
            name: '聯盟廣告',
            type: 'line',
            stack: '總量',
            data: [220, 182, 191, 234, 290, 330, 310]
        },
        {
            name: '視頻廣告',
            type: 'line',
            stack: '總量',
            data: [150, 232, 201, 154, 190, 330, 410]
        },
        {
            name: '直接訪問',
            type: 'line',
            stack: '總量',
            data: [320, 332, 301, 334, 390, 330, 320]
        },
        {
            name: '搜索引擎',
            type: 'line',
            stack: '總量',
            data: [820, 932, 901, 934, 1290, 1330, 1320]
        }
      ]
    }
  }
  render(){

    return (
      <div style={{height:'100%',textAlign:'center',fontSize:20}}>
        <ReactECharts
          option={this.getOption()}
          style={{ height: '60%', width: '90%',margin:'0 auto',paddingTop:'2%' }}
        />
      </div>
    )
  }
}